import {graphql, useStaticQuery} from 'gatsby';
import React from 'react';
import {Helmet} from 'react-helmet';

interface MetaName {
  content: unknown;
  name: string;
}

interface MetaProperty {
  content: unknown;
  property: string;
}

type Meta = MetaName | MetaProperty;

interface Props {
  description?: string;
  keywords?: string[] | string;
  lang?: string;
  meta?: Meta[];
  title?: string;
}

function SEO({
  description,
  lang,
  meta,
  keywords,
  title,
}: Props = {
  description: undefined,
  keywords: [],
  lang: 'en',
  meta: [],
  title: undefined,  
}): React.ReactElement {
  const {
    site: {
      siteMetadata: {title: siteTitle, description: siteDescription, author},
    },
  } = useStaticQuery(graphql`
    query DefaultSEOQuery {
      site {
        siteMetadata {
          title
          description
          author
        }
      }
    }
  `);

  const metaDescription = description ?? siteDescription;
  const attributes = lang ? {htmlAttributes: {lang}} : {};
  return (
    <Helmet
      {...attributes}
      title={title}
      titleTemplate={'%s | ${siteTitle}'}
      defaultTitle={siteTitle}
      meta={[
        {
          content: metaDescription,
          name: 'description',
        },
        {
          content: title,
          property: 'og:title',
        },
        {
          content: metaDescription,
          property: 'og:description',
        },
        {
          content: 'website',
          property: 'og:type',
        },
        {
          content: 'summary',
          name: 'twitter:card',
        },
        {
          content: author,
          name: 'twitter:creator',
        },
        {
          content: title,
          name: 'twitter:title',
        },
        {
          content: metaDescription,
          name: 'twitter:description',
        },
      ]
        .concat(
          keywords
            ? {
                content: Array.isArray(keywords)
                  ? keywords.join(', ')
                  : keywords,
                name: 'keywords',
              }
            : [],
        )
        .concat(meta ?? [])}
    />
  );
}

export default SEO;
